.users_list {
  padding: 1.5rem;
  .ul_header  {
    border-radius: .625rem;
    padding: .9375rem 1.25rem;
    background: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: .625rem;
    .ul_header_lt {

    }
    .ul_header_gt {
      display: flex;
      &>* + * {
        margin-left: .9375rem;
      }
    }
  }
  .ul_content {
    padding: 1.25rem;
    border-radius: .625rem;
    background: #FFFFFF;
    .operate_box {
      display: flex;
      align-items: center;
      justify-content: center;
      .devide_line {
        width: .0625rem;
        min-width: .0625rem;
        height: .75rem;
        background-color: #D8D8D8;
        margin: 0 .625rem;
      }
    }
  }
}

.ul_form {
  &>div {
    display: block !important;
  }
  .ul_form_title {
    font-size: .875rem;
    margin: .75rem 0 .5rem;
  }
  .ul_form_info {
    font-size: .75rem;
    color: red;
  }
}